<template>
  <div>
    <!-- 设置返回按钮 START-->
    <header>
      <a @click="cancel">取消</a>
      <span>发布作品</span>
      <a @click="publishWork">发布</a>
    </header>
    <!-- 设置返回按钮 END -->
    <section>
      <div class="task_title">{{taskInfo.title}}</div>
      <div class="textarea_content">
        <textarea name="work_content" id="work_content" placeholder="请输入您的内容......." v-model="content"></textarea>
      </div>
      <div class="font_num">0/150</div>
      <div class="upload_file">
        <div class="imgs_uploaded">
          <span v-for="(img,index) in imgList" :key="index">
                  <img :src="img" alt="上传图片">
                  <span @click="__remove_img($event,img,index)">×</span>
          </span>
          <div class="file_container" v-if="img_arr && img_arr.length <9">
            <input type="file" id="file" class="file" name="file" @change="changeImg($event)" ref="file">
            <img src="../../../static/images/plus_upload.png" alt="自定义上传组件">
          </div>
          <div class="file_container" v-for="(imgItem, index) in img_arr" :key="index">
            <span class="file_deleteicon" @click="deleteicon(index)">×</span>
            <img :src="imgItem" alt="作业图片">
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        params: {
          content: null,
          filename: null,
        },
        base64: [],
        base: new Array(),
        imgList: [],
        content: '',
        jobInfo: {
          imgList: []
        },
        base64_container: [],
        taskInfo: {},
        img_arr: [],
        editStatus: 0
      }
    },
    methods: {
      cancel() {
        this.$router.back(-1)
      },
      // 保存作业
      publishWork() {
        if (this.content.length == 0 || this.content.charAt(0) == ' ') {
          this.$Toast({
            message: '发布文字内容不能为空',
            position: 'middle',
            duration: 1000
          })
          return
        }
        if (this.editStatus == 0) {
          this.$Indicator.open({
            text: '正在保存作品',
            spinnerType: 'fading-circle'
          })
          this.axios.get('/api/user/addWork', {
            params: {
              content: this.content,
              imagesUrl: this.img_arr.join(',')
            }
          }).then(data => {
            this.$Indicator.close()
            this.$Toast({
              message: '保存作品成功',
              position: 'middle',
              duration: 1000
            })
            setTimeout(() => {
              this.$router.push('/myWorkCircleCenter')
            }, 1000)
          }).catch(err => {
            console.log(err)
          })
        } else if (this.editStatus == 1) {
          this.$Indicator.open({
            text: '正在保存作品',
            spinnerType: 'fading-circle'
          })
          this.axios.get('/api/user/updateWork', {
            params: {
              content: this.content,
              worksId: this.$route.params.id,
              imagesUrl: this.img_arr.join(',')
            }
          }).then(data => {
            this.$Indicator.close()
            this.$Toast({
              message: '保存作品成功',
              position: 'middle',
              duration: 1000
            })
            setTimeout(() => {
              this.$router.push('/myWorkCircleCenter')
            }, 1000)
            console.log(data)
          }).catch(err => {
            console.log(err)
          })
        }
      },
      // 添加图片,追加到数组容器
      changeImg($event) {
        if ($event.target.files.length > 0) {
          this.base64.push($event.target.files)
          const img_url = window.webkitURL ? window.webkitURL.createObjectURL($event.target.files[0]) : window.URL.createObjectURL($event.target.files[0])
          this.jobInfo.imgList.unshift(img_url)
          var reader = new FileReader()
          reader.readAsDataURL($event.target.files[0])
          reader.onload = (event) => {
            this.axios.post('/api/upload_file', {
              content: event.target.result
            }).then((data) => {
              console.log(data)
              this.img_arr.unshift(data.data.data[0])
            })
          }
        }
      },
      deleteicon(index) {
        this.img_arr.forEach((element, i) => {
          if (i == index) {
            this.img_arr.splice(index, 1)
          }
        })
      },
    },
    mounted() {
      if (this.$route.params.id !== 'none') {
        this.editStatus = 1
        this.axios.get('/api/getWorkInfo', {
          params: {
            worksId: this.$route.params.id
          }
        }).then(data => {
          var workObj = data.data.result.WorksE
          this.content = workObj.content
          if (workObj.contentPics) {
            workObj.contentPics.split(',').forEach(element => {
              this.img_arr.push(element)
            })
          }
        }).catch(err => {
          console.log(err)
        })
      } else {
        console.log('发布error')
      }
    }
  }
</script>

<style lang="less" scoped>
  body {
    background-color: #eee;
  } // 发布作品 顶部NAV START
  header {
    display: -webkit-flex;
    display: flex;
    background-color: #fff;
  }
  header * {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    padding: .625rem 0;
  }
  header *:nth-child(2) {
    font-size: 1.25rem;
  }
  header *:nth-child(1) {
    color: #aaa;
  }
  header *:nth-child(3) {
    color: #39bd6c;
  } // 发布作品 顶部NAV END
  section {
    width: 100%;
    margin-top: .625rem;
    height: 100%;
    background-color: #fff;
    padding: 0 0 .625rem 0;
    .textarea_content {
      width: 100%;
      text-align: center;
    }
    .textarea_content>textarea {
      width: 90%;
      height: 9.375rem;
      resize: none;
      border: none;
      outline: none;
    }
    .textarea_content>textarea::-webkit-input-placeholder,
    textarea::-moz-placeholder {
      font-size: 1rem;
    }
    .font_num {
      text-align: left;
      width: 100%;
      padding: .625rem 1.25rem;
      box-sizing: border-box;
      color: #aaa;
    }
    .upload_file {
      .imgs_uploaded {
        padding: .625rem 1.25rem;
        &>span {
          display: inline-block;
          width: 6.25rem;
          height: 6.25rem;
          position: relative;
          &>img {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding: .3125rem;
          }
          &>span {
            display: inline-block;
            width: .9375rem;
            height: .9375rem;
            background-color: red;
            color: #fff;
            text-align: center;
            line-height: .9375rem;
            border-radius: 50%;
            position: absolute;
            top: .625rem;
            right: .625rem;
          }
        }
        &>.file_container {
          text-align: left;
          box-sizing: border-box;
          position: relative;
          display: inline-block;
          width: 6.25rem;
          height: 6.25rem;
          .file_deleteicon {
            position: absolute;
            top: 0;
            right: 0;
            width: 1.25rem;
            height: 1.25rem;
            border-radius: 50%;
            background-color: #f00;
            color: #fff;
            text-align: center;
            line-height: 1.25rem;
            font-size: 1.25rem;
          }
          .file {
            position: absolute;
            top: .625rem;
            left: 1.1875rem;
            width: 6.25rem;
            height: 6.25rem;
            opacity: 0;
            filter: opacity(0);
            overflow: hidden;
          }
          img {
            width: 6.25rem;
            height: 6.25rem;
          }
        }
      }
    }
  } // 课程标题
  .task_title {
    width: 100%;
    height: 2.5rem;
    line-height: 2.5rem;
    text-align: left;
    text-indent: 1em;
    color: #39bd6c;
  }
</style>



